body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.banner {
  background-color: red;
}

.about {
  background-color: yellow;
}

.mywork {
  background-color: pink;
}

.getintouch {
  background-color: gray;
}

/* Handle css */

#fp-nav.fp-right {
  left: 17px;
}

html #fp-nav ul li a span, html .fp-slidesNav ul li a span {
  position: absolute;
  border-radius: 0;
  z-index: 1;
  height: 6px;
  width: 52px;
  border: 0;
  background: #ffffff;
}

#fp-nav ul li:hover a.active span, #fp-nav ul li a.active span, .fp-slidesNav ul li:hover a.active span, .fp-slidesNav ul li a.active span {
  height: 7px !important;
  width: 82px !important;
  margin: -3px 0 0 -6px !important;
  border-radius: 0 !important;
  background-color: #ce4159;
}

.heade-section {
  position: fixed;
  z-index: 99;
  right: 0;
  left: 0;
  background-color: #b3b399;
  padding: 20px;
}

.logo span {
  font-size: 24px;
  letter-spacing: 0.800em;
  font-weight: 600;
}

.fp-tableCell h2 {
  text-align: center;
  font-size: 50px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

